html, body {
  width: 100%;
  height: 100%;
  overflow-y: hidden;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: center;
      justify-content: center;
}

#root {
  width: 100%;
  height: 100%;
}

.App {
  width: 100%;
  height: 100%;
  background-color: #fff;
  overflow-y: scroll;
  overflow-x: hidden;
}

.nav {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  padding: 15px;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: fixed;
  bottom: 5px;
}

.nav div {
  width: 44px;
  height: 44px;
  border-radius: 48px;
  border: 1px #000 solid;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  cursor: pointer;
}

.nav i {
  font-size: 20pt;
}

#menu-panel {
  display: none;
}

#menu-panel.active {
  display: block;
}

input {
  position: absolute;
  bottom: 0;
}

::-webkit-input-placeholder {
  font-size: 14pt;
  color: #666;
  font-weight: normal;
}

::-moz-placeholder {
  font-size: 14pt;
  color: #666;
  font-weight: normal;
}

:focus {
  outline: none;
}

::-moz-selection {
  background: #e4e4e4;
}

::selection {
  background: #e4e4e4;
}

::-moz-selection {
  background: #e4e4e4;
}

i {
  -webkit-user-select: none;  /* Chrome 49+ */
  -moz-user-select: none;     /* Firefox 43+ */
  -ms-user-select: none;      /* No support yet */
  user-select: none;          
}


